import React, { useState } from 'react';
import './carousel.css';
import NHL from '../../assets/NHL.jpg';
import {IntroText} from '../IntroText';

export const CarouselNHLImage = () => {
    // const [ image_frame, setImageFrame ] = useState( 1 ); //between 1 and 4
    // setImageFrame(_image_frame);
    const [ image_opacity, setImageFade ] = useState(0);

    if(image_opacity < 1){
        setTimeout( () => {
            setImageFade(image_opacity +0.1);
        }, 80 );
    }

    let style_object={ "opacity": image_opacity.toString() };

    let image_div = <div style={style_object} className="carousel-image nhl"><IntroText color="white" text="Ensure your sponsorships are visible" /></div>

    //let image_div = <img style={style_object} className='carousel-image' src={ NHL } />;

    return (
        <>
            { image_div }
        </>
    )
}