import React, { useState } from 'react';
import './carousel.css';
import hectic_meeting from '../../assets/hectic-meeting.jpg';
import {IntroText} from '../IntroText';

export const CarouselHecticMeetingImage = () => {
    // const [ image_frame, setImageFrame ] = useState( 1 ); //between 1 and 4
    // setImageFrame(_image_frame);

    const [ image_opacity, setImageFade ] = useState(0);

    if(image_opacity < 1){
        setTimeout( () => {
            setImageFade(image_opacity +0.1);
        }, 80 );
    }
    
    let style_object={ "opacity": image_opacity.toString() };

    let image_div = <div style={style_object} className="carousel-image meeting"><IntroText color="black" text="Take charge of your data" /></div>
    
    //let image_div = <img style={style_object} className='carousel-image' src={ hectic_meeting} />;

    return (
        <>
            { image_div }
        </>
    )
}