import React, { useContext, useState } from 'react';

import './carousel.css';

export const RightSelectorTab = (props) => {
   const [right_fade, setRightFade ] = useState( 20 );
   const [ opacity, setOpacity ] = useState( 0.2 );

   const fadeLeft = () => {
        if( right_fade < 100 ){
            setRightFade( right_fade + 20 );
        }

        if(opacity <  1){
            setOpacity(opacity + 0.2);
        }   
   }

   setTimeout( () => fadeLeft(), 60 );
   let right_position = right_fade;
   console.log("right fade:"+right_position);
   let style_object={ 
       "right":  right_position.toString()+"px",
       "opacity": opacity.toString()
     };

    return (
    <>
        <div style={style_object} className="right-selector" href="#" onClick={ () => props.tabPressed() }></div>
    </>
        )
}