import React, { useState } from 'react';
import './carousel.css';
import { CarouselChicagoImage } from './CarouselChicagoImage';
import { CarouselHecticMeetingImage } from './CarouselHecticMeetingImage';
import { CarouselWrigleyImage } from './CarouselWrigleyImage';
import { CarouselNHLImage } from './CarouselNHLImage';
import { CarouselSelectionWidget } from './CarouselSelectionWidget';
import { LeftSelectorTab } from './LeftSelectorTab';
import { RightSelectorTab } from './RightSelectorTab';

export const ImageCarousel = () => {
    const rotation_duration = 6; //seconds
    const [ stop_carousel, stopCarousel ] = useState( false );
    const [ image_frame, setImageFrame ] = useState( 1 ); //between 1 and 4
    const [click_timeout, setClickTimeout] = useState( 0 ); //seconds/rotation_duration to wait before starting carousel
    const [mouse_in, setMouseIn] = useState(false);

    const max_frames = 4;
    let selection_tab_right = <></>;
    let selection_tab_left = <></>;

    const frameEntryCallback = (frame) => { //used by CarouselSelectionWidget
        setImageFrame( frame );
        stopCarousel( true );
        setClickTimeout(10); //wait 20 seconds
    }

    const leftTabbed = () => {
        console.log("left tabbed");
        if(image_frame === 1){
            setImageFrame( 4 );
        }else{
            setImageFrame( image_frame - 1 );
        }
        
        stopCarousel( true );
        setClickTimeout(10); //wait 20 seconds
    }

    const rightTabbed = () => {
        console.log("right tabbed");
        if(image_frame === 4){
            setImageFrame( 1 );
        }else{
            setImageFrame( image_frame + 1 );
        }
        
        stopCarousel( true );
        setClickTimeout(10); //wait 20 seconds
    }

    const rotateFrame = (frame) => { //between 1 and 4
        if(frame < max_frames){
            return frame + 1;
        }else{
            return 1
        } 
    }

    //main 
    setTimeout(() => {
        if( stop_carousel == false ){ //rotates images unless stopped
            setImageFrame(rotateFrame(image_frame));
        }else{
            if(click_timeout <= 0){
                stopCarousel( false );
            }else{
                setClickTimeout( click_timeout - 1 );
            }
        }
    }, rotation_duration*1000 );



   if(mouse_in === true){
    selection_tab_right = <RightSelectorTab tabPressed={rightTabbed} />
    selection_tab_left = <LeftSelectorTab tabPressed={leftTabbed} />
   }else{
    selection_tab_right = <></>;
    selection_tab_left = <></>;
   }

    let current_image;
    switch(image_frame){
        case 1:
            current_image = <CarouselHecticMeetingImage />;
            break;
        case 2:
            current_image = <CarouselNHLImage />;
            break;
        case 3:
            current_image = <CarouselChicagoImage />;
            break;
        case 4:
            current_image = <CarouselWrigleyImage />;
            break;
        default:
            current_image = <CarouselHecticMeetingImage />;
            break;
    }

    return (
        <>
            <div onMouseEnter={ () => setMouseIn(true)} onMouseLeave={ () => setMouseIn(false)} className='carousel-image-container'>
                {current_image}
                {selection_tab_left}
                {selection_tab_right}
                <CarouselSelectionWidget current_frame={ image_frame } setFrame={frameEntryCallback} />
            </div>
        </>
    )
}