import React, { useContext, useState } from 'react';

import './carousel.css';

export const CarouselSelectionWidget = (props) => {
    let frame_one_widget_classes = "widget-button";
    let frame_two_widget_classes = "widget-button";
    let frame_three_widget_classes = "widget-button";
    let frame_four_widget_classes = "widget-button";
    switch(props.current_frame){
        case 1:
            frame_one_widget_classes = "widget-button highlighted-button";
            break;
        case 2:
            frame_two_widget_classes = "widget-button highlighted-button";
            break;
        case 3:
            frame_three_widget_classes= "widget-button highlighted-button";
            break;
        case 4:
            frame_four_widget_classes = "widget-button highlighted-button";
            break;
        default:
            break;
    }

    return (
    <>
       <div className='widget-container'>
            <div className={frame_one_widget_classes} href="javascript:;" onClick={() => props.setFrame(1) }></div>
            <div className={frame_two_widget_classes} href="#" onClick={() => props.setFrame(2) }></div>
            <div className={frame_three_widget_classes} href="#" onClick={() => props.setFrame(3) }></div>
            <div className={frame_four_widget_classes} href="#" onClick={() => props.setFrame(4) }></div>
        </div>
    </>
        )
}