import React, { useContext, useState } from 'react';

import './carousel.css';

export const LeftSelectorTab = (props) => {
   const [left_fade, setLeftFade ] = useState( 20 );
   const [ opacity, setOpacity ] = useState( 0.2 );

   const fadeLeft = () => {
        if( left_fade < 100 ){
            setLeftFade( left_fade + 20 );
        }

        if(opacity <  1){
            setOpacity(opacity + 0.2);
        }   
   }

   setTimeout( () => fadeLeft(), 60 );
   let left_position = left_fade;
   console.log("left fade:"+left_position);
   let style_object={ 
       "left":  left_position.toString()+"px",
       "opacity": opacity.toString()
     };

    return (
    <>
        <div style={style_object} className="left-selector" onClick={ () => props.tabPressed() }></div>
    </>
        )
}