import React, { useState } from 'react';
import '../App.css';

export const IntroText = (props) => {

    const [ image_opacity, setImageFade ] = useState(0);
    let height_multiple;
    let height_minimum;

    if(image_opacity < 1){
        setTimeout( () => {
            setImageFade(image_opacity +0.1);
        }, 60 );
    }

    var window_width = window.innerWidth
        || document.documentElement.clientWidth
        || document.body.clientWidth;

    if(window_width < 650){
        height_multiple = 9;
        height_minimum = 9;
    }else{
        height_multiple = 15;
        height_minimum = 15;
    }
    
    let style_object={ 
        "opacity": image_opacity.toString(), 
        "font-size": image_opacity*height_multiple + height_minimum,
        "color":props.color //white if none
    };

    let text_output=  <h1 style={ style_object } className="intro-text"> {props.text} </h1>
    //<div style={style_object} className="carousel-image meeting"></div>
    
   

    return (
        <>
            {text_output}
        </>
    )
}