import React, { useState } from 'react';
import './carousel.css';
import wrigley from '../../assets/wrigley.jpg';
import {IntroText} from '../IntroText';

export const CarouselWrigleyImage = () => {
    // const [ image_frame, setImageFrame ] = useState( 1 ); //between 1 and 4
    // setImageFrame(_image_frame);
    const [ image_opacity, setImageFade ] = useState(0);

    if(image_opacity < 1){
        setTimeout( () => {
            setImageFade(image_opacity + 0.1);
        }, 80 );
    }

    let style_object={ "opacity": image_opacity.toString() };

    let image_div = <div className="carousel-image wrigley"><IntroText color="red" text="How much is this add making?" /></div>
    
    //let image_div = <img style={style_object} className='carousel-image' src={ wrigley } />;

    return (
        <>
            { image_div }
        </>
    )
}