import React, { useState } from 'react';
import './carousel.css';
import chicago from '../../assets/chicago.jpg';
import {IntroText} from '../IntroText';

export const CarouselChicagoImage = () => {
    const [ image_opacity, setImageFade ] = useState(0);

    if(image_opacity < 1){
        setTimeout( () => {
            setImageFade(image_opacity +0.1);
        }, 80 );
    }
    
    let style_object={ "opacity": image_opacity.toString() };

    let image_div = <div style={style_object} className="carousel-image chicago"><IntroText text="Made in Chicago" /></div>

    //let image_div = <img style={style_object} className='carousel-image' src={ chicago } />;

    return (
        <>
            { image_div }
        </>
    )
}