import React, { useContext, useState } from 'react';
import './intro.css';
import { TheAngledBox } from '../TheAngledBox';
import { IntroText } from '../IntroText';

export const Intro = () => {

    const [ image_opacity, setImageFade ] = useState(0);

    if(image_opacity < 1){
        setTimeout( () => {
            setImageFade(image_opacity +0.1);
        }, 80 );
    }
    
    let style_object={ "opacity": image_opacity.toString() };

    return (
            <>
                <div className="oversize-mask">
                    <div className='intro-element'>
                       <IntroText color="white" text="Take your sponsorship revenue to new heights" />
                    </div>
                    <div className="intro-bottom-border" ></div>
                </div>
            </>
        )
}