import React from 'react';
import MathJax from 'react-mathjax2'; //it's a typescript file. It's ok.

import './math-equation.css';

export const MathEquation = (props) => {

    return (
    <>
           <MathJax.Context input='ascii'>
                <div className={'math-eq '+props.classes}><MathJax.Node>{ props.equation_ascii }</MathJax.Node></div>
            </MathJax.Context>
    </>
        )
}
