import React, { useState } from 'react';
import base_baller from '../../assets/baseballer.png';

import './service-offers.css';
import 'aos/dist/aos.css'; // animate on scroll library

import {MathEquation} from './math-equation';

export const ServiceOffers = () => {

    const equation_one = 'U = 1/(R_(si) + sum_(i=1)^n(s_n/lambda_n) + R_(se))';
    const equation_two = 'sum_(i=1)^n i^3=((n(n+1))/2)^2';
    const equation_three = 'v_e = ((2GM)^(1/2))/r';
    const equation_four = 'Ff = μη';
    const equation_five = 'a = (v² - v₀²) / 2(x - x₀)';

    return (
    <>
    <div className="service-offers-container">
        <MathEquation equation_ascii={equation_one} classes="eq-one" />
        <MathEquation equation_ascii={equation_two} classes="eq-two" />
        <MathEquation equation_ascii={equation_three} classes="eq-three" />
        <MathEquation equation_ascii={equation_four} classes="eq-four" />
        <MathEquation equation_ascii={equation_five} classes="eq-five" />
        
        <div data-aos="fade-in" className="home-run-container">
            <h1 className="tic-title"> Hit it out of the park with valuation analytics </h1>
                <img className="home-run-batter" src={base_baller} />
                <div className="technology-information-container">
                    <div className="technology-information-box-l">
                        <div className="info-box-context">
                            <h3> Find Your Most Profitable Sponsorships </h3>
                            <p> Process large amounts of data into comprehensive reports </p>
                            
                        </div>
                    </div>
                    <div className="technology-information-box-r">
                        <div className="info-box-context">
                            <h3> Make Informed Decisions </h3>
                            <p> Use impressions to see if your sponsorships are a hit </p>
                        </div>
                    </div>
                    <div className="technology-information-box-l">
                        <div className="info-box-context">
                            <h3> Pick the Winning Sources of Revenue </h3>
                            <p> Optimise your sponsorships </p>
                            <p> Profit from your data </p>
                            
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>
        )
}